
#app {
  min-height: 100vh;
  font-family: 'Roboto', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.placeholder {
  margin-top: 24vh;
}
.content section {
  margin-bottom: 50px;
}
@media screen and (max-width: 767px) {
.content section {
    margin-bottom: 20px;
}
}
.content .brand .title {
  font-weight: 300;
  font-family: 'Oxygen', 'Roboto', 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 36px;
}
@media screen and (max-width: 767px) {
.content .brand .title {
    font-size: 20px;
}
}
.content .brand .title .name {
  font-size: 56px;
}
@media screen and (max-width: 767px) {
.content .brand .title .name {
    font-size: 32px;
}
}
.content .brand .subtitle {
  font-size: 28px;
  font-weight: 300;
}
@media screen and (max-width: 767px) {
.content .brand .subtitle {
    font-size: 18px;
}
}
.content .links .link {
  font-weight: 300;
  font-family: 'Oxygen', 'Roboto', 'Avenir', Helvetica, Arial, sans-serif;
  color: #333;
  font-size: 24px;
  line-height: 30px;
  display: block;
}
@media screen and (max-width: 767px) {
.content .links .link {
    font-size: 18px;
}
}
.content .links .link a {
  color: #333;
  -webkit-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}
.content .links .link a:hover {
  text-decoration: none;
}
.content .links .link a:hover +.link-sub {
  color: #aaa;
}
.content .links .link .link-sub {
  font-size: 20px;
  font-family: 'Roboto', 'Avenir', Helvetica, Arial, sans-serif;
  font-weight: 200;
  margin-left: 15px;
  color: rgba(0,0,0,0);
  -webkit-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  user-select: none !important;
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  cursor: default;
}


